export default {
  data() {
  return {
    ingredients: [{"name":"ABC","qnty":"1","unit":""}],
    title: "abc",
    people: 1,
    instructies: [{"beschrijving":"abc"}],
    options: ['g','kg','L','mL','tsp','tbsp'],
    errors: {},
    file: null,
    filepath: '',
    source: '',
  }
  },
  methods: {
  addIngredient() {
      this.ingredients.push({"name": '','qnty':''});
  },
  removeIngredient(index) {
      this.ingredients.splice(index,1);
  },
  addStap() {
      this.instructies.push({"beschrijving":""});
  },
  removeStap(index) {
      this.instructies.splice(index,1);
  },

  handleFile(e) {
      this.file = e.target.files[0];
  },

  async uploadImage() {
      let fd = new FormData();
          fd.append('image',this.file);
          return this.$axios.post('http://192.168.2.248:3333/api/recipes/upload-image',fd).then(response => response.data
          ).catch(error => {
              this.errors = error.response.data;
          });
  },

  async addRecipe() {
      if(typeof(this.file) !== 'undefined' && this.file !== null && this.filepath.length === 0) {
           this.filepath = await this.uploadImage();
      }
      this.$axios.post('http://192.168.2.248:3333/api/recipes', {
          ingredients: this.ingredients,
          title: this.title,
          people: this.people,
          instructies: this.instructies,
          imagepath: this.filepath,
          source: this.source,
      }).then(function (response) {
          console.log(response); 
      }).catch(error => {
          console.log("ERROR: ", error.response.data);
          this.errors = error.response.data;
      });
  }
  }
}
